<!-- 详细博客 -->

<template>
    <div>
        <h1>{{ blog.name }}</h1>
        <span>作者：</span>
        <span>{{ blog.userName }}</span><br>
        <span>发布时间：</span>
        <span>{{ blog.date }}</span><br>
        <div v-html="blog.content" class="html"></div>
        <el-input v-model="form.text" placeholder="发表评论"></el-input>
        <el-button type="primary" @click="submit(0)">评论</el-button>
        <el-card class="box-card" v-for='(ListPiece, index) in List' :key="index">
            <div v-for='(ListOne, index) in ListPiece' :key="index">
                <div v-if="index == 0">
                    <h3>{{ ListOne.userName }} 评论: {{ ListOne.text }}</h3>
                    <el-popover placement="bottom" title="标题" width="200" trigger="click">
                        <el-button slot="reference">评论</el-button>
                        <el-input v-model="form.text" placeholder="发表评论"></el-input>
                        <el-button type="primary" @click="submit(ListOne.id)">评论</el-button>
                    </el-popover>
                </div>
                <span v-if="index != 0">{{ ListOne.userName }} 回复: {{ ListOne.text }}</span>

            </div>
        </el-card>
    </div>
</template>

<script>
import axios from 'axios';
const options = {
    inject: ['reload'],
    data() {
        return {
            blog: this.$store.state.blog,
            List: '',
            form: {
                text: "",
                date: new Date(),
                blogId: this.$store.state.blog.id,
                userName: this.$store.state.name,
                seniorId: 0,
            },
            ban: '',
            commentShow: false,
        }
    },
    methods: {
        getBlogComment() {
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/comment/blogComment",
                method: 'post',
                data: this.$store.state.blog.id,
                headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
            })
                .then(res => {

                    this.List = res.data;
                });
        },
        submit(seniorId) {
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/user/selectByName?name=" + this.$store.state.name,
                method: 'get',
                headers: { 'Content-Type': 'application/json' }
            })
                .then(res => {
                    this.ban = new Date(res.data.banday) < new Date;
                    if (this.ban) {
                        this.form.seniorId = seniorId;
                        axios.request({
                            baseURL: "http://localhost:8090/",
                            url: "/comment/addBcomment",
                            method: 'post',
                            data: this.form,
                            headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
                        })
                            .then(res => {
                                this.$message("评论成功");
                                this.form.text = '';
                                this.reload();
                            });
                    } else {
                        this.$message("您已被禁言");
                    }
                });


        },
    },
    mounted: function () {
        this.getBlogComment();
    }

}
export default options;

</script>
<style>
.html {
    background-color:antiquewhite;
    width: 80%;
}
</style>